<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <div class="icon-wrapper">
          <i class="back" @click="back"></i>
        </div>
        <div class="search-box">
          <search-box @query="onQueryChange" placeholder="请输入日期/关键词"></search-box>
        </div>
      </div>
      <div class="content-wrapper">
        <ul class="tab" ref="tab">
          <li class="tab-item active" @click="selectTab('')"><span>全部</span></li>
          <li class="tab-item" @click="selectTab('11')"><span>待签收</span></li>
          <li class="tab-item" @click="selectTab('51')"><span>已完成</span></li>
        </ul>
        <scroll class="content">
          <ul class="list">
            <li class="item" v-for="(item,index) in list" @click="selectItem(item)">
              <div class="left">
                <p>{{item.creationTime|formatDate}}</p>
                <p>{{item.creationTime|formatTime}}</p>
              </div>
              <div class="right">
                <div class="order detail-item">
                  <p class="code"><label>配送单ID：</label><span>{{item.code}}</span></p>
                  <p><label></label><span class="red">{{item.status|formatDeliverStatus}}</span></p>
                </div>
                <div class="price detail-item">
                  <p><label>品种：</label><span class="red">{{item.kindCount}}</span></p>
                  <p><label>总价：</label><span class="red bold">{{item.totalMoney}}</span><label>元</label></p>
                </div>
                <p class="btn">
                  <span @click.stop="confirm(item)" v-if="item.status != 51">签收</span>
                </p>
              </div>
            </li>
          </ul>
        </scroll>
      </div>
      <alert ref="alert" :message="alertMsg"></alert>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import Alert from 'base/alert/alert'
  import SearchBox from 'base/search-box/search-box'
  import {addClass,removeClass} from 'common/js/dom'
  import {ERR_CODE} from 'api/config'
  import {getMerchantDeliveryOrder,merchantDeliveryOrderConfirm} from 'api/purchase'

  export default {
    data () {
      return {
        alertMsg: '',
        query: '',
        list: [],
        status: ''//'',11,51
      }
    },
    created () {
      this._getMerchantDeliveryOrder()
    },
    filters: {
      formatDate(val){
        if(!val){
          return ''
        }
        return val.substring(0,10)
      },
      formatTime(val){
        if(!val){
          return ''
        }
        return val.substring(11)
      }
    },
    computed: {
      currentRoute(){
        return this.$router.currentRoute.path
      }
    },
    methods: {
      back() {
        this.$router.push({
          'path': '/purchase'
        })
      },
      showAlert() {
        this.$refs.alert.show()
      },
      _getMerchantDeliveryOrder() {
        getMerchantDeliveryOrder({
          status: this.status
        }).then((res) => {
          if(res.errCode === ERR_CODE){
            this.list = res.data
          }
        })
      },
      confirm(item) {
        merchantDeliveryOrderConfirm({
          merchantDeliveryOrderId: item.id
        }).then((res) => {
          if(res.errCode === ERR_CODE){
            this.$router.push({
              path: '/purchase'
            })
          }
        })
      },
      selectItem(item) {
        this.$router.push({
          path: `${this.currentRoute}/${item.id}`
        })
      },
      onQueryChange(newQuery) {
        this.query = newQuery
      },
      selectTab(status){
        this.status = status
        let tab = this.$refs.tab
        let children = tab.children
        let total = children[0]
        let ready = children[1]
        let finish = children[2]
        if(status == ''){
          addClass(total, 'active')
          removeClass(ready, 'active')
          removeClass(finish, 'active')
        } else if(status == '11'){
          removeClass(total, 'active')
          addClass(ready, 'active')
          removeClass(finish, 'active')
        } else if(status == '51'){
          removeClass(total, 'active')
          removeClass(ready, 'active')
          addClass(finish, 'active')
        }
      }
    },
    watch: {
      status(){
        this._getMerchantDeliveryOrder()
      },
      query() {
        this._getMerchantDeliveryOrder()
      }
    },
    components: {
      SearchBox,
      Scroll,
      Alert
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
        margin-right 33px
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      flex-direction column
      .tab
        display flex
        height 45px
        flex-shrink 0
        border-top 1px solid #E6E6E6
        background #fff
        color #4c4c4c
        font-size $font-size-medium-m
        width 100%
        .tab-item
          display flex
          flex 1
          justify-content center
          span
            display flex
            flex-direction column
            justify-content center
          &.active
            span
              color #eb4747
              position relative
              &:after
                position absolute
                height 2px
                background #eb4747
                bottom 0
                left 0
                right 0
                content ''
      .content
        margin-top 6px
        background #fff
        max-height 100%;
        overflow hidden
        .list
          .item
            height 125px
            display flex
            box-sizing border-box
            border-bottom 1px solid #f2f2f2
            margin-left 10px
            margin-right 10px
            &:last-child
              border none
            .left
              display flex
              flex-direction column
              color #999
              font-size $font-size-small
              padding-top 20px
              width 64px
              margin-right 8px
              p
                margin-top 4px
                width 100%
                text-align center
            .right
              display flex
              flex 1
              flex-direction column
              .detail-item
                height 28px
                display flex
                align-items center
                color #666
                font-size $font-size-medium-m
                .red
                  color #eb4747
                .bold
                  font-size $font-size-medium-x
                  font-weight bold
              .order
                margin-top 10px
                justify-content space-between
                .code
                  max-width 220px
                  white-space nowrap
                  text-overflow ellipsis
                  display flex
                  span
                    text-overflow ellipsis
                    display inline-block
                    width 150px
                    overflow hidden
              .price
                justify-content space-between
              .btn
                margin-top 16px
                color #eb4747
                display flex
                justify-content flex-end
                span
                  align-items center
                  display flex
                  justify-content center
                  width 70px
                  height 28px
                  box-sizing border-box
                  border 1px solid #f7a6a4
                  border-radius 4px

</style>




